.left-nav {
    width: 150px;
    min-height: 100vh;
    background: #666666;
    padding: 20px 10px;
  }
  
  .left-nav ul li a {
    padding: 0 0 8px;
    border-bottom: 1px dotted #939393;
    width: 100%;
    color: #fff;
    margin-bottom: 8px;
    display: inline-block;
    text-decoration: none;
  }
  
  .button-row button {margin: 0 10px 30px 0;}
  .content { display: flex;  justify-content: space-between;}
  .right-content {width: calc(100% - 150px);  padding: 20px;}
  .box-row { display: flex;  flex-wrap: wrap;}
  .box-cnt {border: 1px solid #666;margin-bottom: 30px; padding: 10px;}
  
  .unlock-btn {display: flex; justify-content: space-between; align-items: center;}
  
  ul#menu > li{
    float:left;
    list-style-type:none;
    position:relative; width: 100%;;
  }
  
  .left-nav label {
    color: #fff;
    border-bottom: 1px dotted #939393;
    padding-bottom: 10px;
  }
  
  label{
    position:relative;
    display:block;
    cursor:pointer;
  }
  
  label:after{
    content:"";
    position:absolute;
    display:block;
    top:50%;
    right:5px;
    width:0;
    height:0;
    border-top:4px solid rgba(255,255,255,.5);
    border-bottom:0 solid rgba(255,255,255,.5);
    border-left:4px solid transparent;
    border-right:4px solid transparent;
    transition:border-bottom .1s, border-top .1s .1s;
    }
  
  label:hover,
  input:checked ~ label{}
  
  input:checked ~ label:after{
    border-top:0 solid rgba(255,255,255,.5);
    border-bottom:4px solid rgba(255,255,255,.5);
    transition:border-top .1s, border-bottom .1s .1s;
  }
  
  /*hide the inputs*/
  #check01{display:none}
  
  /*show the second levele menu of the selected voice*/
  input:checked ~ ul.submenu{
    max-height:300px;
    transition:max-height 0.5s ease-in;
  }
  
  /*style for the second level menu*/
  ul.submenu {
    max-height: 0;
    padding: 0;
    overflow: hidden;
    list-style-type: none;
    box-shadow: 0 0 1px rgb(0 0 0 / 30%);
    transition: max-height 0.5s ease-out;
    min-width: 100%;
  }
  
  ul.submenu li a {
    display: block;
    padding: 5px 0 13px;
    color: #ddd;
    text-decoration: none;
    transition: background .3s;
    white-space: nowrap;
  }
  
