body{background:#f9faff; box-sizing: border-box;}
*{margin: 0; padding:0;}
p{margin-bottom: 0;}
.tab-row a {padding: 10px 20px;color: #000;text-decoration: none;}
.maine-content {margin-top: 20px;}
.tab-row { display: flex;  width: 100%;  margin-bottom: 20px;border-bottom: 1px solid #ebebeb;}
.user-name-row img {width: 35px; height: 35px; border-radius: 100%;margin-right: 10px;}
.row-box { display: flex;  margin-top: 10px;  width: 100%; align-items: center;}
.user-name-row { display: flex;}
.button-row button {background: #35c8c2;border: 0;}
.tab-col {border-radius: 10px; padding: 20px; box-shadow: 0px 0px 9px 0px #ededed;margin-bottom: 30px; background:#fff;}
.button-row { border-top: 1px solid #f9faff;  margin: 15px -20px 0;   padding: 20px 20px 0;}
.text-heading {font-size: 12px; color: #b5b5b5;  font-weight: 500; margin-right: 15px;}
.amount {font-size: 16px; font-weight: 500; color: #000;}
.font-18{font-size: 18px; font-weight: 500; color: #000;}
.user-name-row p span {color: #b5b5b5;}
.user-name-row p {font-weight: 500;}

.finished-row { background: #f2f2f2; border-radius: 5px; padding: 10px;  display: flex; align-items: center; margin:15px 0 0;}
.finished-row .icon {background: #fff; padding: 6px; width: 30px; height: 30px;border-radius: 100%;display: flex;align-items: center;justify-content: center;margin-right: 15px;}
.finished-row .icon img { width: 30px; height: 30px; border-radius: 100%;}
.finished-row button {background: #35c8c2; border: 0; outline: none;}
.button-strict {background-color: #35c8c2 !important; border: 0 !important; outline: none;}
button {background-color: #35c8c2; border: 0 !important; outline: none;}
.finished-row.finished {background: #35c8c2;}
.finished-row.finished  button{background: #fff; color:#000;}
.finished-row button span { width: 8px;  height: 8px;  border-radius: 100%; background: #000; display: inline-block;}
.finished-amt {display: flex; align-items: center;  justify-content: space-between; margin-top:5px;}
.finished-amt p { font-size: 12px; font-weight: 500;}
.finished-amt p.amt{color:#b5b5b5 }
.tab-row a.active { color: #424242;  border-bottom: 2px solid #35c8c2;}

.deal-row {align-items: center;  margin-bottom:15px;}
.font-18{color:#b5b5b5; font-size:18px; margin-right:10px;}
.mt-0{margin-top:0 !important;}
.total-box { display: flex;  align-items: center;}
.progress-bar-row {margin: 20px -20px 0; border-top: 1px solid #f9faff;  padding: 20px 20px 0;}
.progress {display: flex;height: 10px;}
.progress-bar {background-color: #35c8c2 !important;}
.deal-info ul li { list-style: none;  margin-top: 6px;}
.col-100{margin-bottom:20px;}
.descr-btn a { color: #35c8c2;  margin-top: 20px;  display: inline-block;}
.dl-dt { margin-top: 10px !important;}
.heading-sm {margin-bottom: 15px; font-weight: 500;}
button.cnt-button { width: 100%;  font-size: 22px;}

.login-box {background: #fff; padding: 20px; border-radius: 10px; min-width: 320px;box-shadow: 0px 0px 12px 0px #259d98;}
.login {width: 100%;min-height: 100vh;display: flex;align-items: center;justify-content: center;background: #35c8c2;}
.login-box button {width: 100%;}
.nt-member { font-size: 12px;  margin-top: 5px;}
.nt-member a {color: #b2b4b4;}
.login-box h1 {font-size: 34px; color: #040404;  margin-bottom: 20px;}
.input-design {background-color: transparent !important;outline: none;}

.user-img img {width: 40px;height: 40px; border-radius: 100%; margin-right:10px;}
.user-img {display: flex; align-items: center; margin-top: 30px;}
.submit-btn  {display: flex;justify-content: flex-end; align-items: center;}
.submit-btn button { margin: 0 10px;}
form.deal-form {background: #fff;padding: 20px; margin:10px 0 20px; border-radius: 10px; box-shadow: 0 0 12px 0 #f3f3f3; }
.page-heading{font-size: 34px; color: #040404;  margin-bottom: 20px;}



.connect-wallet{float: right;text-align: right;}
.bg-wallet{border: 1px solid #b5b5b5; border-radius: 25px;background-color: white;padding: 5pt 15pt 8pt 15pt;}
.amount-sm{font-size: 15px; font-weight: 500; color: #000;}
.text-heading {font-size: 12px; color: #b5b5b5;  font-weight: 500; margin-right: 15px;}
#contributionAmount {width: 80%; margin: 0 0 10px; height: 45px; border: 1px solid #c8c8c8; border-radius: 5px; text-align: center; font-size: 18px;}
.contributionAmount-maxButton{float: right; width: 18%; margin: 0 0 10px; height: 45px; font-size: 18px; background-color: #c0c1c2 !important;}

.overlay-box {position:absolute; top:50%; left:50%; transform:translate(-50%, -50%); z-index: 1000; }

.parentDisable { position: fixed; top: 0; left: 0; opacity: 0.8; z-index: 998; height: 100%; width: 100%; pointer-events: none; }
.hide{ display: none;}
.buy-div{ align-content: center;padding: 50px 0; text-align: center; }
.uniswap{width:180px; background-color:pink !important;}
.pancakeswap{width:180px; background-color:gold !important;}
.btn-right { float: right; margin: 0 10px;}
.hidden-table{margin: 2px 0;}

.previous {
    background-color: #f1f1f1;
    color: black;
    padding: 0px 17px;
    border-radius: 50%;
    vertical-align:text-bottom;
  }
.previous:hover {
    background-color: #ddd;
    color: black;
}
.page-heading{font-size: 34px; font-weight: 500; margin-right: 15px;display: inline;}
.previous img{margin-bottom: 5px;}
